<template>
  <v-app>
    <v-container>
      <v-row>
        <v-col cols="12" md="4">
          <v-btn
            class="mr-4"
            type="submit"
            color="primary"
            @click="authenticate"
          >
            Authorizar <v-icon>mdi-login</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="12" md="4">
          <v-btn class="mr-4" type="submit" color="primary" @click="excute">
            Ejecutar
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
