      },
      {
        name: "Mooncraft",
        img:
          "https://yt3.ggpht.com/ytc/AAUvwnjCrsvO1L1kR5CVjJejZbIdCWORfKcdvRYivTfU8g=s88-c-k-c0x00ffffff-no-rj",
        pc: "mooncraft.es",
      },
      {
        name: "HyCraft",
        img:
          "https://yt3.ggpht.com/ytc/AAUvwniItYTVKevfu3Sp8SWF8dIC5uAOTgeB8HFUPweJfQ=s88-c-k-c0x00ffffff-no-rj",
        pc: "mc.hycraft.us",
      },
      {
        name: "HonlyCraft",
        img:
          "https://yt3.ggpht.com/ytc/AAUvwni4e6g3VYz7vk2uI0oc-dFLkHISkm63pubYmlZ3=s88-c-k-c0x00ffffff-no-rj",
        pc: "mc.honlycraft.us",
      },
      {
        name: "Splindux",
        img:
          "https://cdn.discordapp.com/attachments/688566101910880275/830494735956836382/636564639831776656.png",
        pc: "mc.splindux.com",
      },
    ],
    moderators: [],
    mySubscribersYT: [],
    myFollowersTwitch: [],
    myDonators: [],
  }),
  sockets: {
    async connect() {
      console.log("Esta conectado");
    },

    async disconnect() {
      console.log("Esta desconectado");
    },
    changeMessageRandom: async function (data) {
      this.messageSub = data;
    },
  },
  methods: {
    getDiscord() {
      var api = this.$store.state.URL + "/channels/myDiscordDetails";

      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.myDiscord = data.myDiscord;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    timeOut(variable) {
      this[variable] = true;
      setTimeout(() => {
        this[variable] = false;
      }, 3000);
    },
    copyText(text) {
      var aux = document.createElement("input");
      aux.setAttribute("value", text);
      document.body.appendChild(aux);
      aux.select();
      document.execCommand("copy");
      document.body.removeChild(aux);
    },
    changeMessageRandom() {
      this.$socket.emit("messageRandom", this.messageSub);
    },
    getMyChannel() {
      var api = this.$store.state.URL + "/channels/myChannels";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.channel = data.myChannel;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getTexturePack() {
      var api = this.$store.state.URL + "/channels/texturePack";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.texturePack = data.texturePack;
          this.texturePack = this.texturePack.sort(function (a, b) {
            return a.channelName.localeCompare(b.channelName);
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getMods() {
      var api = this.$store.state.URL + "/channels/modYoutube";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.moderators = data.moderators;
          this.moderators = this.moderators.sort(function (a, b) {
            return a.channelName.localeCompare(b.channelName);
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDonators() {
      var api = this.$store.state.URL + "/channels/myDonations";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data.myDonators;
        })
        .then((myDonators) => {
          this.myDonators = myDonators;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getSubscribers(max = 7) {
      var api = this.$store.state.URL + "/channels/mySubscribersYT/" + max;
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data.mySubscribers;
        })
        .then((mySubscribers) => {
          this.mySubscribersYT = mySubscribers;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getFollowers(max = 7) {
      var api = this.$store.state.URL + "/channels/myFollowTwitch/" + max;
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data.myFollowers;
        })
        .then((myFollowers) => {
          this.myFollowersTwitch = myFollowers;
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.getDiscord();
    this.getMyChannel();
    this.getMods();
    this.getTexturePack();
    this.getSubscribers(10);
    this.getDonators();
    this.getFollowers(10);
